* {
  margin: 0%;
  padding: 0%;
}
.notfound {
  width: 100%;
  height: 100%;
  margin: 50px auto;
  display: block;
  padding: 10px;
}

.notfound__image {
  width: 100%;
  height: 100%;
}
.notfound__heading {
  text-align: center;
  font-family: sans-serif;
  padding: 20px;
  color: #f73378;
}

#backtohomelink {
  text-decoration: none;
  font-size: 20px;
  font-family: sans-serif;
  padding: 10px;
  display: block;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .notfound__heading {
    font-size: 20px;
  }
  .notfound {
    width: 80%;
    height: 400px;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
  #backtohomelink {
    margin-top: 100px;
  }
}

@media screen and (min-width: 1024px) {
  .notfound__heading {
    font-size: 30px;
  }
  .notfound {
    width: 80%;
    height: 550px;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
}
